	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
                newsList:[],
                idType:0,
                isZH:true
			};
		},
        created:function () {
            //判断显示中/英文
            if(localStorage.LANGUAGE!='zh'){
                this.isZH = false;
            }
            let _this = this
	        let params = {
	            hotelid: localStorage.HOTELID,
                lang: localStorage.LANGUAGE,
                page:1,
                limit:5,
                token:localStorage.TOKEN
            }
            //员工消息
            this.idType = localStorage.idType;
            if(2==localStorage.idType){
                _this.$store.dispatch('getStaffAppMsgList', params).then(function (res) {
                    _this.newsList = res.data.list;
                });
            }else{//住户消息
                _this.$store.dispatch('getNews',params).then(function (res) {
                    _this.newsList = res.data.list;
                });
            }

            $(function(){
                $(".navbar-center").css('marginLeft',0);
            });
        },
		methods: {
			goDetail(url){
                // if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                //     openFile(url)
                // }else{
                //     if(url.indexOf('pdf')>0){
                //         openPdf(url)

                //     } else {
                //         openUrl(url,'')
                //     }
                // }
            }
		},
        mounted:function () {
            //一级页面falg
            isHomePage(0)
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })

        },
	};



